$color-grey: #E1E1E1;
$color-dark-grey: #AFAFAF;
$color-yellow: #EABF00;
$color-white: #FFFFFF;
$color-black: #1E1E1E;
$color-brown: #130F0A;
$color-blue-light: #9FE8FA;
$color-blue-dark: #26BAEE;

$shadow: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));


@function randomNum($min, $max) {
    $rand: random();
    $randomNum: $min + floor($rand * (($max - $min) + 1));
  
    @return $randomNum;
  }


@keyframes showContact {
    from {
        height: 0px;
    }

    to {
        height: 500px;
    }
}


@keyframes hideContact {
    from {
        height: 500px;
    }

    to {
        height: 0px;
    }
}


html {
    font-size: 10px;
    max-width: 100%;
    overflow-x: hidden;

    @media only screen and (max-width: 900px) {
        font-size: 6px;
        overflow-x: hidden;
        .section {
            margin: 5rem;
        }
        .divider {
            margin: 10rem;
        }

        .section__traits {
            grid-template-columns: repeat(3, 1fr);
column-gap: 0rem;
row-gap: 0;
        }
        .resume {
            margin: 5px;
        }
    }

}

body {
    margin: 0px;
    font-family: "ABeeZee";
    max-width: 100%;
    overflow-x: hidden;
}

h1 {
    font-family: "Akaya Telivigala";
}

h2 {
    font-family: "Akaya Telivigala";
}

.emphasize {
    font-family: "Akaya Telivigala";
    font-weight: 700;
}

.root {

}

.container {
    position: relative;
}

input, input:focus, input:active, textarea {
    border-radius: 20px;
    border-color: $color-black;
    padding: 5px;
    outline: none;
    border-width: 0px;
}

.circle {
    height: 100rem;
    width: 100rem;
    position: absolute;
    border-radius: 100%;
    z-index: -100;
    filter: $shadow;

    &__grey {
        background-color: $color-grey;
        top: -50rem;
        right: -50rem;
    }

    &__brown {
        background-color: $color-brown;
        top: -50rem;
        left: -100rem;
    }

    &__blue {
        background-color: $color-blue-light;
        top: -50rem;
        right: -100rem;
    }
    
}

.header {
    display: flex;

    &__image {
        flex: .2;
        text-align: center;
        margin-top: 2rem;

        & img {
            border-radius: 500px;
            width: 15rem;
        }

    }

    &__content {
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;

        &_container {

            & h1 {
                font-size: 5rem;
                
                &:first-child {
                    text-align: center;
                    font-size: 7rem;
                }

            }

            & h2 {
                font-size: 4rem;
                margin-left: 20px;
            }

            & h3 {
                font-size: 3rem;
                margin-left: 40px;
            }

            & p {
                font-size: 2rem;
            }

            .full-stack {
                display: flex;
                align-items: center;
                margin-left: 60px;
                & h3 {
                    margin-left: 10px;
                }
            }
        }

    }

    &__empty {
        flex: .2
    }
}

.contact { 
    position: fixed;
    top: 10px;
    right: 10px;
    background-color: $color-grey;
    border-radius: 20px;
    z-index: 100;

    &__show {
        width: 350px;
        height: 500px;
        animation-name: showContact;
        animation-duration: 1s;
        animation-timing-function: ease-out;
    }

    &__hide {
        width: 350px;
        height: 0px;
        animation-name: hideContact;
        animation-duration: 1s;
        animation-timing-function: ease-in;
    }

    &__close {
        width: 0px;
        height: 0rem;
    }

    &__content {
        overflow: hidden;
        display: flex;
        flex-direction: column;
    }

    h2 {
        font-size: 50px;
        margin: 0;
        align-self: center;
        text-align: center;
    }

    &__icon {
        position: absolute;
        top: 10px;
        right: 10px;
        height: 30px;
        width: 30px;
        img {
            width: 100%;
            height: 100%;
        }
    }

    &__form {
        justify-content: center;
        align-items: center;
        display: flex;
    &__content{
        width: 300px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        & > * {
            margin-top: 10px;
            width: 100%;
        }

        & input, textarea {
            padding: 10px;
        }

        & label {
            font-size: 14px;
        }

        input:last-child {
            margin-top: 20px;
            background-color: $color-dark-grey;
            padding: 10px;
            font-size: 14px;
        }
    }

    &__message {
        height: 100px;
    }
}
}

.divider {
    margin: 20rem;
    h1 {
        font-size: 7rem;
        text-align: center;
        margin: auto;
    }
}

.section {
    margin: 20rem;
    position: relative;

    & > * {
        margin-top: 10rem;
    }

    &__header h1 {
        font-size: 5rem;
        text-align: center;
    }

    &__description {
        margin-left: 2rem;

        p {
            font-size: 2rem;
        }

        &__text {
            &__emphasize {
            font-size: 3rem;
            }
        }
    }

    &__image {
        &__first {
            width: 100%;
            height: 60rem;
            background: url('./images/books-and-brewskies.jpeg');
            background-size: auto;
            border-radius: 10px;
        }
        &__second {
            width: 100%;
            height: 60rem;
            background: url('./images/letslink-preview.png');
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center;
            border-radius: 10px;
        }
    }

    &__traits {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        column-gap: 5rem;
        row-gap: 5rem;

        & > * {
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 500px;
            justify-self: center;
            p {
                text-align: center;
                color: $color-white;
                font-size: 2rem;
            }
        }

        &__item {
            &_first:nth-child(even) {
                background-color: $color-brown;
            }
    
            &_first:nth-child(odd) {
                background-color: $color-yellow;
            }

            &_second:nth-child(even) {
                background-color: $color-blue-dark;
            }
    
            &_second:nth-child(odd) {
                background-color: $color-blue-light;
            }
    
        }

    }

}

.resume {
    position: relative;
    display: flex;
    justify-content: center;
    margin: 20rem;
    &__content {
        position: relative;
        height: 100rem;
        width: 75rem;
        filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
        .c {
            position: absolute;
            top: 0;
            width: 100%;
            height: 100%;
            cursor: pointer;
            will-change: transform, opacity;
          }
          
          .front,
          .back {
            background-size: cover;
          }
          
          .back {
            background-image: url('./images/resumeFront.jpg');
          }
          
          .front {
            background-image: url('./images/resumeBack.jpg');
          }
    }
}